import React from 'react';
import Slider from 'react-slick';
import VideoPlayer from '../VideoPlayer/VideoPlayer';
import Aux from '../Aux/Aux';
// import Slide from '../Slide/Slide';
// import PhotoArray from '../../photo-imports';
import videoSource from '../../video/yucho-chow-anim_1.mp4';

// CSS
import './Carousel.css';

// media
import chevronRight from '../../images/chevron-right.png';





export default class Carousel extends React.Component {
	constructor(props) {
		super(props);

		this.next = this.next.bind(this);
		this.prev = this.prev.bind(this);
	}

	next() {
		this.slider.slickNext();
	}

	prev() {
		this.slider.slickPrev();
	}



	render() {
		const settings = {
			arrows: false,
			beforeChange: (prev, next) => {this.props.beforeChange(prev, next)},
			dots: true,
			fade: true,
			infinite: false,
			lazyLoad: 'progressive', // or 'ondemand'
			mobileFirst: true,
			slidesToShow: 1,
			slidesToScroll: 1,
			speed: 500,
			swipeToSlide: true
		};

		const buttonContainerStyle = {
			bottom: 0,
			left: 0,
			position: 'absolute',
			right: 0,
			textAlign: 'center',
			top: 40
		}

		const prevStyle = {
			float: 'left',
			height: '93vh',
			width: '25%'
		}
		const nextStyle = {
			float: 'right',
			height: '93vh',
			width: '73%'
		}


		return (
			<Aux>
			<Slider ref={slider => (this.slider = slider)} {...settings}>
				<div id="slide-00" className="slide-content">
					<div className='title-block'>
						<h3>Yucho Chow</h3>
						<p className='slide-copy'><span>Vancouver‘s first Chinese Canadian photographer chronicled the lives of the city’s marginalized and minority communities.</span></p>
					</div>
					<footer>
							<p>Tap to continue... <img className="arrow-button" src={chevronRight} alt=" " /></p>
							
					</footer>
				</div>

				<div id="slide=01" className="video-background slide-content">
					<div className='copy-block'>
						<p className='slide-copy'><span>Yucho Chow was the go-to photographer for marginalized communities and visible minorities in Vancouver.</span></p>
						<p className='slide-copy'><span>He photographed Black Canadians, Sikhs, Europeans and First Nations as well as Chinese, documenting lives that were largely excluded from the historical record.</span></p>
					</div>
					<VideoPlayer
						source={videoSource}
					></VideoPlayer>
				</div>

				<div id="slide-01a" className="slide-content">
					<div className='title-block'>
						<p className='slide-copy'><span>These images are some of Chow‘s recently found photos, painstakingly collected from archives, family albums and second-hand shops.</span></p>
					</div>
				</div>

				<div id="slide-02" className="slide-content">
					<div className='copy-block'>
						<p className='slide-copy'><span>Four unidentified Sikh men, some time in the 1920s.</span></p>
					</div>
				</div>

				<div id="slide-03" className="slide-content">
					<div className='copy-block'>
						<p className='slide-copy'><span>“Chinatown Soccer Team” in the 1920s.</span></p>
					</div>
				</div>

				<div id="slide-04" className="slide-content">
					<div className='copy-block'>
						<p className='slide-copy'><span>An unidentified black couple pose in front of one of Chow’s iconic art-deco backdrops.</span></p>
					</div>
				</div>

				<div id="slide-05" className="slide-content">
					<div className='title-block'>
						<p className='slide-copy'><span>For over four decades, Chow recorded thousands of people, photographing family portaits, weddings, entertainers and others.</span></p>
					</div>
				</div>

				<div id="slide-06" className="slide-content">
					<div className='copy-block'>
						<p className='slide-copy'><span>A hand-tinted wedding portrait.</span></p>
					</div>
				</div>

				<div id="slide-07" className="slide-content">
					<div className='copy-block'>
						<p className='slide-copy'><span>Emily Collins and her grandchildren in 1944. Emily defied convention by marrying Charles Collins, a Black-Jamaican, at a time when mixed-race families were uncommon and rarely documented.</span></p>
					</div>
				</div>

				<div id="slide-08" className="slide-content">
					<div className='copy-block'>
						<p className='slide-copy'><span>Alexander Klimec and his family in 1937. The Klimec’s were regulars at Yucho Chow Studio, having their photos taken more than 20 times.</span></p>
					</div>
				</div>

				<div id="slide-07a" className="slide-content">
					<div className='copy-block'>
						<p className='slide-copy'><span>A woman holding money decorations.</span></p>
					</div>
				</div>

				<div id="slide-08a" className="slide-content">
					<div className='copy-block'>
						<p className='slide-copy'><span>A hand-tinted image of Richard Mar, circa 1945. Mar was the first Chinese Canadian selected for the elite 1st Canadian Paratroop Battalion.</span></p>
					</div>
				</div>
				
				<div id="slide-10" className="slide-content">
					<div className='title-block'>
						<p className='slide-copy'><span>Despite a large body of work, there are few Yucho Chow photos in public collections or archives. His negatives were discarded when his family closed the studio in 1986.</span></p>
					</div>
				</div>

				<div id="slide-credits" className="slide-content">
					<div className='title-block'>	
						<p className='slide-copy'><span>Images courtesy of Yucho Chow, the City of Vancouver Archives and the Chinese Canadian Artifacts Project.</span></p>
						<p className='slide-copy'><span>Produced and developed by Nathan Griffiths with files from Kevin Griffin.</span></p>
						<p className='slide-copy'><span>© Postmedia 2019.</span></p>
					</div>
				</div>
			</Slider>

			<div style={buttonContainerStyle}>
				<div className="button prev-button" onClick={this.prev} style={prevStyle}></div>
				<div className="button next-button" onClick={this.next} style={nextStyle}></div>
			</div>
			</Aux>
		);
	}
}
