import React from 'react';
import Aux from '../Aux/Aux';
import Carousel from '../Carousel/Carousel';

export default class TapCarousel extends React.Component {
	constructor(props) {
		super(props);

		this.beforeChangeHandler = this.beforeChangeHandler.bind(this);

		
	}

	componentDidMount() {
		const slides = document.getElementsByClassName('slick-slide');

		this.setState({ slideCount: slides.length });
	}

	beforeChangeHandler(prev, next) {
		console.log(`Before change: ${next}, ${prev}`);
		const nextButton = document.getElementsByClassName('next-button');

		if (next >= this.state.slideCount - 1) {
			nextButton[0].style.display = 'none';
		} else {
			nextButton[0].style.display = 'block';
		}
	}

	render() {
		return (
			<Aux>
				<div>
			    	<Carousel
			    		beforeChange={this.beforeChangeHandler}
			    	></Carousel>
			    </div>
	    	</Aux>
		);
	}
}